<!-- 服务记录 -->
<template>
  <div class="service-record-box flex-column">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isCustom='false'></Breadcrumb>
    </div>
    <!-- 主要内容 -->
    <div class="inner-box flex-column flex-overflow">
      <!-- 选项卡 -->
      <div class="tabs-box">
        <ul>
           <li v-for="(item,index) in serviceTypeOption" :key="index" :class="currentIndex===item.value?'active':''" @click="indexChange(item.value)">{{item.label}}</li>
        </ul>
      </div>
      <!-- 服务列表 -->
      <div class="service-list flex-overflow">
        <div class="service-box" v-for="(item,index) in serviceList" :key="index">
           <!-- 签约记录 -->
           <div v-if="currentIndex===1" class="yellow-box">
              <p>签约包名称：{{item.servicePackageName||EMPTYTEXT}}</p>
              <p>签约时间：{{item.signDateStr||EMPTYTEXT}}</p>
              <p>签约医生：{{item.registerUser||EMPTYTEXT}}</p>
           </div>
           <!-- 履约记录 -->
           <div v-if="currentIndex===4" class="green-box">
              <p>服务包：{{item.servicePackageName||EMPTYTEXT}}</p>
              <p>服务项目：{{item.servicePackageItemName||EMPTYTEXT}}</p>
              <p>服务时间：{{item.createDateStr||EMPTYTEXT}}</p>
              <p>服务医生：{{item.registerUser||EMPTYTEXT}}</p>
           </div>
            <!-- 解约记录 -->
           <div v-if="currentIndex===2" class="red-box">
              <p>解约服务包：{{item.servicePackageName||EMPTYTEXT}}</p>
              <p>解约时间：{{item.terminateDateStr||EMPTYTEXT}}</p>
              <p>解约医生：{{item.registerUser||EMPTYTEXT}}</p>
           </div>
        </div>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :pageNum="pageSize" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name:'service-record',
    data() {
      return {
        serviceTypeOption:[
          {
            value:1,
            label:'签约记录'
          },
          {
            value:4,
            label:'履约记录'
          },
          {
            value:2,
            label:'解约记录'
          }
        ], //签约类型
        currentIndex:1, //当前索引
        pageNum:1,//当前页
        pageSize:12,//每页条数
        totalNum: 0, //总条数
        serviceList:[] //服务记录列表
      };
    },
    created() {
      this.getDatalist() //获取服务记录列表
    },
    methods: {
      //选项卡改变
      indexChange(index){
        this.currentIndex = index
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取服务记录列表
      getDatalist(){
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params.serviceType = this.currentIndex
        params.recordId = this.$route.query.id
        this.$http.post(process.env.VUE_APP_API + '/family/service/getByPage', params).then(res => {
          if (res.code===200) {
            this.serviceList = res.data.list || [] //居民信息
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      }
    }
};
</script>

<style lang="scss" scoped>
.service-record-box{
  .inner-box{
    padding:30px;
   /*  选项卡 */
    .tabs-box{
      margin-bottom:30px;
      ul{
        width: 310px;
        height: 38px;
        background-color: #f7f2e9;
        backdrop-filter: 0.4;
        box-shadow: inset 0px 3px 7px 0px rgba(34, 113, 194, 0.1);
        border-radius: 19px;
        display:flex;
        align-items:center;
        justify-content:center;
        li{
          width: 98px;
          height: 30px;
          line-height:30px;
          text-align:center;
          border-radius: 15px;
          cursor: pointer;
          &.active{
            background-image: linear-gradient(90deg, #60e6a8 0%, #80f5cd 100%);
            box-shadow: 0px 5px 13px 0px rgba(16, 154, 5, 0.2);
            color:#fff;
          }
        }
      }
    }
    /* 服务列表 */
    .service-list{
       display:flex;
       flex-wrap:wrap;
      .service-box{
        margin-bottom:23px;
        width:25%;
        min-width:390px;
        height:180px;
        padding:0px 40px 20px 0px;
        box-sizing:border-box;
        >div{
          background-color: #ffffff;
          box-shadow: 0px 5px 10px 1px 
            rgba(123, 64, 1, 0.1);
          border-radius: 10px;
          width: 100%;
          height: 100%;
          box-sizing:border-box;
          padding:24px 30px;
          display:flex;
          flex-direction: column;
          justify-content:space-between;
          transition: all 0.3s;
          border-radius: 3px 0px 0px 3px;
          &:hover{
            transform:translateY(-10px);
          }
          &.yellow-box{
            border-left:4px solid #fcca6e;
            background:url("../../../assets/images/yellow-card.png") 100% 100%/cover no-repeat;
          }
          &.red-box{
            border-left:4px solid #fc936e;
            background:url("../../../assets/images/red-card.png") 100% 100%/cover no-repeat;
          }
          &.green-box{
            border-left:4px solid #6ce7ae;
            background:url("../../../assets/images/green-card.png") 100% 100%/cover no-repeat;
          }
        }
      }
    }
  }
}
</style>
